export interface FullSchema {
    type: string,
}

export function getFullSchema(dispatch: any): FullSchema {
    sendrequist(dispatch);
    return {
        type: "getFullSchema",
    }
}

export function getChangeSchema(dispatch: any): FullSchema {
    return {
        type: "getFullSchema",
    }
}







function sendrequist(dispatch: any) {
    return new Promise((resolve, reject) => {
        let p1 = fetch("http://127.0.0.1/getFullSchema", {
            method: "POST",
        }).then((res: any) => {
            return res.json();
        }).then((res: any) => {
            dispatch({
                type: "getFullSchemaSccess",
                payload: res
            });

        })

    })
}




