import * as actions from "../actions/index"
import * as React from "react"
import { Store } from "../types/index"
import { connect, Dispatch } from "react-redux"
import "../css/index.css"
import RightContainer from "./RightContainer"
export function mapStateToProps({ demo: { languageName, payload } }: Store) {

    return {
        name: languageName,
        payload: payload
    };
}

export function mapDispatchToProps(dispatch: Dispatch<actions.FullSchema>) {
    return {
        getFullSchema: () => dispatch(actions.getFullSchema(dispatch))
    }
}
export interface Props {
    name: string;
    payload?: any;
    getFullSchema?: () => void;
}
export interface State {
    currentCountry: any
}

class Index extends React.Component<Props, State>{

    constructor(args: Props) {
        super(args)
        this.state = {
            currentCountry: "AU"
        }
    }
    componentDidMount() {
        this.props.getFullSchema();
    }

    changeCountry(event: any) {
        if (event.target.getAttribute("attr-country")) {
            this.setState({
                currentCountry: event.target.getAttribute("attr-country")
            })
        };

    }

    render() {

        if (this.props.payload) {
            var countryArr = this.props.payload["beneficiary.bank_details.bank_country_code"].func_desc
            countryArr = countryArr.split(', ')
            countryArr[0] = countryArr[0].replace("IsOneOf([", "");
            countryArr[countryArr.length - 1] = countryArr[countryArr.length - 1].replace("])", "");
            var countary = countryArr.map((country: any, i: number) => {
                return <li key={i} attr-country={country} className={this.state.currentCountry == country ? "hover" : ""} >{country}</li>
            })
        }

        return (
            <article className="container" >
                <header >
                    <span>支付指南v1.0</span>
                </header>
                <article className="mainContainer"  >
                    <div className="leftContaier">
                        <div className="countryList">
                            <ul onClick={this.changeCountry.bind(this)} >
                                {(countary) && countary}
                            </ul>
                        </div>
                    </div>
                    <RightContainer payload={this.props.payload} currentCountry={this.state.currentCountry} />
                </article>
            </article>)
    }
}





function mergeProps(stateProps: Object, dispatchProps: Object, ownProps: Object) {
    return Object.assign({}, ownProps, stateProps, dispatchProps);
}

export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(Index);



