import * as React from "react"
import CustomSelect from "../components/customSelect"

export interface Props {
    payload: any,
    currentCountry: string
}

export interface State {
}

export default class RightContainer extends React.Component<Props, State>{
    constructor(args: Props) {
        super(args);
    }

    render() {
        if (this.props.payload) {
            var requiredTrue = [];
            var requiredFalse = [];
            for (let key in this.props.payload) {
                if (this.props.payload[key].required) {
                    requiredTrue.push(key);
                } else {
                    requiredFalse.push(key);
                }
                var requiredTrueMap = requiredTrue.map((item: any) => {
                    return <dd>{item}</dd>
                })
                var requiredFalseMap = requiredFalse.map((item: any) => {
                    return <dd>{item}</dd>
                })
            }
            var rightContainer = <div className="rightContainer">
                <dl className="countryTitle">
                    {/* <dt className="img"></dt> */}
                    <dd>
                        {/* <p className="title">英国</p>
                    <p>Great Britaln</p> */}
                        <p>国家代码:{this.props.currentCountry}</p>
                    </dd>
                </dl>
                <div className="modelStyle">
                    <div>
                        <div className="line1">
                            <div className="left">支付类型</div>
                            <div>p2p</div>
                        </div>
                        <div className="line1">
                            <div className="left">支持多币种支付</div>
                            AUD
                    </div>


                        <div className="line1">
                            <div className="left">支付方式</div>
                            local
                    </div>

                    </div>
                    <div className="infoRight">
                        <div className="line1 ">
                            <div className="left">银行渠道</div>
                            <p className="p1">支持所有银行账号</p>
                        </div>
                        <div className="line1">
                            <div className="left">交易限额</div>
                            <p className="p1">--</p>
                        </div>
                        <div className="line1">
                            <div className="left">交易附言</div>
                            <p className="p1">--</p>
                        </div>
                    </div>
                </div>
                <div className="infoCode">
                    <div className="bx">
                        <p className="title">必填字段</p>
                        <div>
                            <dl className="infoRight">
                                {requiredTrueMap}
                            </dl>
                        </div>
                    </div>
                    <div className="bx infoRight">
                        <p className="title">选填字段</p>
                        <div>
                            <dl className="infoRight">
                                {requiredFalseMap}
                            </dl>

                        </div>
                    </div>
                </div>
            </div>

        };
        return (
            <div >
                {rightContainer}
            </div>
        )
    }
}


